div.contact{
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	background-repeat:no-repeat;
	background-size:100% 100%;
	background-position:center;
	background-color: #bebec0;
	font-family: jf;
	background-image: url("../images/Gray.svg");
	background-repeat: repeat;
	background-size: 33vw 10.7vw;

	div.master_header{
		//		position: absolute;
		div.master_left{
			div.banner{
				background-image: url('');
				img.back_matser{
					width: 2vw;
				}
			}
		}
	}

	div.center{
		background-color: #179dd9;
		width: 70vw;
		margin: 0 auto;
		height: 77vh;
		border-radius: 3vw;
		padding: 1vw;
		direction: rtl;
		div.dash_line{
			position: relative;
			border: 0.2vw dashed white;
			width: 100%;
			height: 100%;
			border-radius: 3vw;
			padding: 1vw;
			background-image: url("../images/Blue_for_Blue.svg");
			background-size: 33.3vw 10.7vw;
			p.head{
				color: white;
				font-size: 3vh;
				font-weight: bold;
				margin: 0.5vw;
			}

			div.center_info_container{
				position: absolute;
				width: 77%;
				height: 95%;
				top:0;
				left: 0;
				right: 0;
				bottom: -24vh;
				margin: auto;
				border-radius: 3vw;
				div.shadow{
					position: absolute;
					z-index: 1;
					width: 100%;
					height: 60%;
					top: 30%;
					top: 5%;
					border-radius: 3vw;
					box-shadow: 0px 10px 23px 5px rgba(0,0,0,0.2);
				}
				div.center_info{
					background-color: #f1f1f1;
					position: absolute;
					width: 100%;
					height: 100%;
					top:0;
					left: 0;
					border-radius: 3vw;
					z-index: 2;

					div.center_image{
						position: relative;
						margin: 0 auto;
						top: -13vh;
						width: 23vh;
						height: 23vh;
						border-radius: 50%;
						padding: 0.3vw;
						img{
							width: 100%;
						}
					}

					div.profile_container{
						position: absolute;
						width: 100%;
						top: 0;
						padding: 15vh 5vw 0;
						height: 100%;
						text-align: center;
						color: #545555;


						h1, h4{
							font-weight: bold;
						}

						p{
							font-size: 1.8vh;
							font-weight: bold;
						}

						h4{
							font-size: 2.8vh;
							margin-top: 2vh;
							margin-bottom: 2vh;
						}

						div.address{
							border-bottom: 0.2vw solid #cacbcc;
							height: 28%;
							&:last-child{
								border-bottom:none;
							}

						}
					}
				}
			}
		}
	}
}
@import "contact/4-3.scss";
@import "contact/3-2.scss";
@import "contact/16-10.scss";
@import "contact/17-10.scss";
@import "contact/16-9.scss";