div.study{

	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;

	overflow: hidden;
	-ms-content-zooming: none;

	div.master_header{
		position: absolute;
		div.master_left{
			div.banner{
				background-image: url(../images/Global/Arrow_Bannar_Yellow.svg);

			}
		}
	}

	p.subjectTitle{
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		margin: auto;
		padding-top: 2%;
		text-align: center;
		font-size: 2.5vw;
		color: white;
	}

	div.study_background{
		position: absolute;
		width: 100%;
		height: 100%;
		background: linear-gradient(-4deg, rgb(0, 227, 255), rgb(7, 114, 144));
		z-index: -1;
		top : 0;
		left: 0;
		img.star{
			width: 15%;
			position: absolute;
			z-index: 1;

			&:nth-child(1){
				top: 40%;
				left: 7%;
				width: 9%;
			}
			&:nth-child(2){
				top: 5vw;
				left: -1vw;
				width: 9vw;
				margin: 0;
				opacity: 0.5;
			}
			&:nth-child(3){
				top: 59%;
				left: 9%;
				width: 6%;
			}
			&:nth-child(4){
				top: 71%;
				left: -6%;
				width: 20%;
				opacity: 1;
			}
			&:nth-child(5){
				top: -4%;
				left: 35%;
				width: 14%;
			}
		}
	}

	div.study_content{
		position: absolute;
		left: 0;
		top: 8vw;
		width: 100vw;
		height: 90vh;
		z-index: 3;
		display: flex;
		justify-content: flex-start;
		justify-content: flex-end;
		align-items: center;
		flex-direction: row;
		font-family: jf;

		img.Arrow_left-study{
			width: 4%;
			position:absolute;
			z-index: 4;
			left: 3vw;
			top: 47%;
			&.eng{
				right: auto;
				left: 8vw;
			}
		}

		img.Arrow_right-study{
			width: 4%;
			position:absolute;
			z-index: 4;
			right: 8vw;
			top: 47%;
			&.eng{
				left: auto;
				right: 3vw;
			}
		}

		div.study_square{
			position: relative;
			overflow: hidden;
			width: 90vw;
			height: 90vh;
			background-color: white;
			border-radius: 1vw;
			z-index: 3;
			border: 0.5vw solid #00c8ea;

			div.top_shadow{                
				position: absolute;
				z-index: 1;
				background-color: rgba(0,0,0,0.3);
				width: 100%;
				height: 5%;
				border-radius: 12%;
				top: -3%;
				left: 0;
				right: 0;
				margin: auto;
				filter: blur(3px) grayscale(20%);
			}

			div.iframe-study{
				position: absolute;
				width: 100%;
				height: 100%;
				top: 1vw;
				overflow: auto;
				direction: rtl;
				overflow: hidden;
				-ms-content-zooming: none;

				&.eng{
					left: auto;
					direction: ltr;
				}
			}

			div.Panel_search_study{

				position: absolute;
				transition-property: width, right, left;
				transition: 1s;
				z-index : 4;
				overflow: hidden;
				width: 21.3vw;
				height: 35vh;
				top: 9vh;
				right: -21.3vw;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;

				&.eng{
					//					left: auto !important;
					transform: scaleX(-1);
					direction: ltr;
				}

				div.top{
					width: 100%;
					height: 26%;
					background-color: #0072bc;
					border-radius: 1vw;
					z-index: 3;
					border: 0vw solid #0072bc;
					box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.51);
					display: flex;
					align-items:center;
					justify-content: space-around;
					flex-direction: row;

					input.search_textarea_study{
						margin: 0.5vw;
						width: 17.5vw;
						height: 5.8vh;
						font-size: 2vw;
						text-align: center;
						direction: rtl;
						&.eng{
							transform: scaleX(-1);
							direction: ltr;
						}
					}

					div.search_btnsrch_study{
						width: 2.5vw;
						height: 2.5vw;
						background-image: url("../images/search_btnsearch.png");
						background-size: 100% auto;
						background-repeat: no-repeat;
						background-position: center center;
					}
				}

				div.bottom{

					width: 87%;
					height: 82%;
					background-color: #0072bc;
					border-radius: 0.3vw 0.3vw 1vw 1vw;
					z-index: 2;
					border: 0vw solid #0072bc;

					display: flex;
					align-items: center;
					justify-content: space-around;
					flex-direction: row;

					div.search_btnsrch_study{
						background-image: url("../images/search_btnsearch.png");
						position: absolute;
						background-position: center center;
						background-repeat: no-repeat;
						background-size: contain;
					}

					div.search_added {
						direction: rtl;
						overflow-y: auto;
						width: 90%;
						height: 88%;
						div{
							margin: 0.5vw;
						}
						&.eng {
							transform: scaleX(-1);
							direction: ltr;
						}
						ul{
							padding-right: 2vw;
							li{
								color: #EAE0EF;
								text-decoration: none;
							}
						}
						a{
							color: #EAE0EF;
							text-decoration: none;
						}
					}

				}

				&.active{
					width: 23.3vw;
					right: 0vw;
				}
				&.eng{
					transform: scaleX(-1);
					left: -23.3vw;
					&.active_En{
						width: 23.3vw;
						left: 0vw;
					}
				}
				input.text_area_study{
					top: 1.2vh;
					left: 6vw;
					width: 10.5vw;
					height: 3vw;
					direction: ltr;
				}
			}

			//GoTo Panel Style
			div.Panel_goto_study{
				position: absolute;
				transition-property: width, right, left;
				transition: 1s;
				z-index : 4;
				overflow: hidden;
				width: 23.3vw;
				height: 9vw;
				top: 19vh;
				right: -23.3vw;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;


				div.goToBtns{
					width: 4.5vh;
					height: 4.5vh;
				}

				div.top{
					width: 100%;
					height: 50%;
					background-color: #0072bc;
					border-radius: 1vw;
					z-index: 3;
					border: 0vw solid #0072bc;
					box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.51);

					display: flex;
					align-items: center;
					justify-content: center;
					flex-direction: column;
					div.goToBtns{
						&.btn_goto_pg_study{
							left: 1vw;
							top: 1vw;
							position: absolute;
							background-size: cover;
							background-repeat: no-repeat;
							background-image: url("../images/btn_goto_pg.png");
						}
					}

					input.goto_textarea_study{
						//						position: absolute;
						font-size: 2vw;
						text-align: center;
						direction: rtl;
						&.eng{
							transform: scaleX(-1);
							direction: ltr;
						}
					}
				}

				div.bottom{
					width: 84%;
					height: 45%;
					background-color: #0072bc;
					border-radius: 0.3vw 0.3vw 1vw 1vw;
					z-index: 2;
					border: 0vw solid #0072bc;

					display: flex;
					align-items: center;
					justify-content: space-around;
					flex-direction: row;

					div.goToBtns{
						//						position: absolute;
						background-size: 100% 100%;
						background-repeat: no-repeat;

						&.btn_prev_pg_study{
							background-image: url("../images/btn_goto_prev.png");
						}
						&.btn_next_pg_study{
							background-image: url("../images/btn_goto_next.png");
						}
						&.btn_first_pg_study{
							background-image: url("../images/btn_goto_first.png");
						}
						&.btn_last_pg_study{
							background-image: url("../images/btn_goto_last.png");
						}
					}
				}

				&.active{
					width: 20.3vw;
					right: 0vw;
				}
				&.eng{
					transform: scaleX(-1);
					left: -23.3vw;
					&.active_En{
						width: 20.3vw;
						left: 0vw;
					}
				}
				input.goto_textarea_study{
					top: 1.2vh;
					left: 6vw;
					width: 10.5vw;
					height: 3vw;
					direction: ltr;
				}
			}

			div.Panel_bookmark_study{

				position: absolute;
				transition-property: width, right, left;
				transition: 1s;
				z-index : 4;
				overflow: hidden;
				width: 21.3vw;
				height: 35vh;
				top: 30vh;
				right: -21.3vw;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;


				div.top{
					width: 100%;
					height: 22%;
					background-color: #0072bc;
					border-radius: 1vw;
					z-index: 3;
					border: 0vw solid #0072bc;
					box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.51);
					display: flex;
					align-items:center;
					justify-content: space-around;
					flex-direction: row;

					input.text_area_bookmark{
						margin: 0.5vw;
						width: 17.5vw;
						height: 5.8vh;
						font-size: 2vw;
						text-align: center;
						direction: rtl;
						&.eng{
							transform: scaleX(-1);
							direction: ltr;
						}
					}


					div.search_btnsrch_bookmark{
						width: 2.5vw;
						height: 2.5vw;
						background-image: url("../images/btn_bookmark_add.png");
						background-size: 100% auto;
						background-repeat: no-repeat;
						background-position: center center;
					}
				}

				div.bottom{

					width: 87%;
					height: 82%;
					background-color: #0072bc;
					border-radius: 0.3vw 0.3vw 1vw 1vw;
					z-index: 2;
					border: 0vw solid #0072bc;

					display: flex;
					align-items: center;
					justify-content: space-around;
					flex-direction: row;

					div.search_btnsrch_bookmark{
						background-image: url("../images/btn_bookmark_add.png");
						position: absolute;
						background-size: 100% auto;
						background-repeat: no-repeat;
						background-position: center center;


					}


					div.bookmark_added{
						overflow: auto;
						direction: rtl;
						width: 90%;
						height: 88%;
						&.eng{
							transform: scaleX(-1);
							text-align: left;
						}

						ul{
							padding-right: 2vw;
							color: #EAE0EF;
							width: 100%;

							li{
								width: 100%;
								button{
									font-size: 2vw;
									float: left;
									color: white;
									align-self: flex-end;
									opacity: 1;
								}
							}
						}

						div.bookmark_raw{
							width: 100%;
							direction: rtl;
							display: flex;
							align-items: center;
							justify-content: space-between;

							a{
								color: #EAE0EF;
								text-decoration: none;
								width: 90%;
								word-wrap: break-word;
							}

							.cBookmark{
								color: white;
								align-self: flex-end;
								opacity: 1;
							}
						}
					}

				}

				&.active{
					width: 23.3vw;
					right: 0vw;
				}
				&.eng{
					transform: scaleX(-1);
					left: -23.3vw;
					&.active_En{
						width: 23.3vw;
						left: 0vw;
					}
				}
				input.text_area_study{
					top: 1.2vh;
					left: 6vw;
					width: 10.5vw;
					height: 3vw;
					direction: ltr;
				}
			}

			div.Panel_tools_study{

				position: absolute;
				background-color: #0072bc;
				border-radius: 1vw;
				border: 0vw solid #0072bc;
				box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.51);

				transition-property: width, right, left;
				transition: 1s;
				z-index : 4;
				width: 23.3vw;
				height: 4vw;
				bottom: 19vh;
				right: -23.3vw;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;

				div.btn_tool_marker_study, div.btn_tool_pen_study, div.btn_tool_eraser_study, div.add-sticky{
					position: absolute;
					background-repeat: no-repeat;
					background-size: 100% auto;
					height: 3.6vw;
					width: 3.6vw;
					top: 0.3vh;
					background-position: center center;
				}

				div.add-sticky{
					background-image: url("../images/tools_note.png");
					left: 1.2vw;
				}

				div.btn_tool_marker_study{
					background-image: url("../images/tools_marker.png");
					left: 6.4vw;
				}

				div.btn_tool_pen_study{
					background-image: url("../images/tools_marker_pen.png");
					left: 12vw;
				}

				div.btn_tool_eraser_study{
					background-image: url("../images/tools_erase.png");
					left: 17.5vw;
				}

				&.active{
					width: 23.3vw;
					right: 0vw;
				}
				&.eng{
					transform: scaleX(-1);
					left: -23.3vw;
					&.active_En{
						width: 23.3vw;
						left: 0vw;
					}
				}
			}

		}

		div.study_tools{
			position: relative;
			width: 7.5vw;
			height: 90vh;
			display: flex;
			justify-content: space-around;
			align-items: center;
			flex-direction: column;
			border-radius: 1vw;
			z-index: 4;
			padding: 7.6vh 0;

			img{
				width: 9vh;
			}

			div.all_tools_study{
				width: 7vw;
				height: 7vw;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				img{
					display: none;
					&.active{
						display: block;
					}
				}
			}

			div.Pnl_goto_study{

				&.eng{
					transform: scaleX(-1);
					//					left: auto !important;
				}
				input.text_area_study{
					position: absolute;
					font-size: 2vw;
					text-align: center;
					direction: rtl;
					&.eng{
						transform: scaleX(-1);
						direction: ltr;
					}
				}

			}

			div.paging_study{
				text-align: center;
				background-image: url(../images/Page_number_Study.png);
				background-size: cover;
				background-repeat: no-repeat;
				font-size: 1.9vw;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 8vw;
				height: 3.2vw;
				margin-right: 0.7vw;
				font-family: DroidArabicNaskh;
				&.eng{
					left: auto;
					margin-left: 0.3vw;
				}
			}
		}

		&.eng{
			flex-direction: row-reverse;
		}

	}
}

