div.menu{
	display: none;
	//	display: inline-block;
	position: fixed;
	top:0px;
	right: 0px;
	height: 100vh;
	width: 31vw;
	z-index: 3;
	transform: translateX(15vw);
	transition: 1s transform, 1s opacity cubic-bezier(1, 0, 1, 0);
	opacity: 1;

	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

	&.active-menu{
		transform: translateX(0);
		opacity: 1;
		transition: 1s transform, 1s opacity cubic-bezier(0, 1, 0, 1);
	}
	div.seperation_layer{
		height: 100%;
		width: 100%;
		position: absolute;
	}
	.menu_container{
		height: 100%;
		width: 100%;
		position: absolute;
		nav{
			background-color: #343746;
			direction: rtl;
			height: 100%;
			width: 100%;
			ul{
				margin: 0px;
				padding: 0px;
				list-style: none;
				li{

					&.profile{
						display: flex;
						align-items: center;
						padding: 1.5vw 1vw;
						background-color: #f05760;
						//					height: 15vh;

						div.user_image{
							width: 8vw;
							height: 8vw;

							div.circle{
								width: 100%;
								height: 100%;
								border-radius: 50%;
								border: 0.4vw solid white;
								overflow: hidden;
								background-image: url("../images/User_pictuer.png");
								background-size: cover;
							}
						}



						div.user_info{
							font-family: jf;
							font-size: 2.5vh;
							width: 70%;
							text-align: center;
							color: white;
							p{
								margin: 0px;
								&:first-child{
									font-weight: bold;
								}
								//							&:last-child{
								//								word-wrap: break-word;
								//							}
							}
						}
					}
					&.menu_item{
						padding: 1.5vw 3vw;
						border-bottom: 0.2vw solid #615e6b;
						img{
							width: 25vw;
						}
					}

					&:last-child{
						img{
							margin-left: 1.5vw;
							width: 25%;
						}
					}
				}
			}
		}
	}
}


@import "menu/4-3.scss";
@import "menu/3-2.scss";
@import "menu/16-10.scss";
@import "menu/17-10.scss";
@import "menu/16-9.scss";