div.qbank{
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;

	div.master_background{
		background-color: #afca09;
	}

	div.master_header{
		div.master_left{
			div.banner{
				background-image: url(../images/Global/Arrow_Bannar_Yellow.svg);

			}
		}

	}

	div.master_content{

		div.content_square{
			border: 0.5vw solid #678702;

			div.qbankcontainer{
				height: 100%;
				position: relative;
				direction: rtl;
				overflow: auto;
				font-family: DroidArabicNaskh;
				
				&.eng{
					direction: ltr;
				}
				div.question_qbank{
					margin: 2vw 2vw 1vw;
					overflow: auto;

					h3.questionhead{
						text-align: center;
						font-weight: bold;
						font-size: 2.5vw;
					}
					img.bywidth{
						max-width: 100%;
					}
					h4.questiontitle{
						font-size: 2.3vw;
						line-height: 3.5vw;
						font-weight: bold;
					}
					p.questionparagraph{
						font-size: 2.5vw;
						padding: 1vw 2vw;
						line-height: 4vw;
						text-align: justify;
					}
				}
				div.answerBtnDiv{
					display: none;
					text-align: center;
					margin-bottom: 1vw;
					button{
						font-size: 2.5vw;
						width: 35%;
						margin: auto;
						span.glyphicon{
							margin-bottom: 2%;
						}
						span.text{
							margin: 1vw;
						}
					}
				}
				div.answer_qbank{
					margin: 2vw;
					display: none;
					img{
						max-width: 100%;
					}
					p.answerparagraph{
						padding: 1vw 2vw;
						font-size: 2.5vw;
						line-height: 4vw;
						//						text-align: justify;
					}
				}
			}
		}
	}
}
