$checked : #00ad5f;
$inactive : #464646;
$active : #FFFFFF;
$wrong : #a94442;

div.mcq{
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100vh;
    margin: 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;

    div.mcqHeader{
        position: absolute;
        left: 0;
        width: 100vw;
        height: 10vh;
        font-size: 2.5vw;
        top: 0vw;
        display: flex;
        justify-content: center;
        align-items: center;
        div.Timer-Top{
            text-align: center;
            width: 10%;
            font-family: Orbitron;
        }


        div.Header-result{
            display: none;
            width: 50%;
            text-align: center;
            direction: rtl;
            justify-content: space-between;
            //			position: absolute;
            font-family: Orbitron;

            div.Header-result-right{

                display: flex;
                direction: rtl;
                align-items: center;
                justify-content: center;
                p{
                    margin: 0;
                    margin-left: 5px;
                }

            }

            div.Header-result-left{
                display: flex;
                direction: rtl;
                align-items: center;
                justify-content: center;
                p{
                    margin: 0;
                    margin-left: 5px;
                }
            }
        }
    }

    div.master_background{
        background-color: #eac52e;
    }



    div.master_content{
        div.content_square{
            border: 0.5vw solid #ea7202;

            div.mcq_contanier{
                height: 100%;
                position: relative;
                overflow-y: auto;
                margin-top: 1.5vw;
                font-size: 2.5vw;
                z-index: 3;

                div.ExamContainer{
                    direction: rtl;
                    display: block;
                    padding: 0 3vw;

                    div.lineparent{
                        display: none;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        margin-bottom: 1vw;
                        div.line{
                            width: 21vw;
                            height: 0.5vh;
                            background-color: red;
                        }

                        div.linecenter{
                            color: red;
                            font-size: 2vw;
                        }
                    }



                    ul#AnswerId{
                        .icon-x:before {
                            font-weight: normal;
                            font-family: 'icomoon' !important;
                            content: "\1f319";
                        }
                        .icon-checkmark:before {
                            font-weight: normal;
                            font-family: 'icomoon' !important;
                            content: "\1f332";
                        }
                        list-style: none;
                        height: 100%;
                        width: 100%;
                        margin: 0;
                        padding: 0;
                        li{
                            color: $inactive;
                            display: block;
                            position: relative;
                            width: 100%;
                            height: 5vw;
                            input[type=radio]{
                                position: absolute;
                                visibility: hidden;
                                margin: 0;
                                width: 0;
                                height: 0;
                                &:checked{
                                    & ~ div.check{
                                        border: 0.3vw solid $checked;
                                        &:before{
                                            background-color: $checked;
                                        }
                                    }
                                    & ~ label{
                                        color: $checked;
                                    }
                                }
                            }
                            label{
                                display: flex;
                                align-items: center;
                                position: relative;
                                font-weight: 300;
                                font-size: 2.6vw;
                                padding-right: 6vw;
                                margin: 1vw auto;
                                height: 100%;
                                z-index: 9;
                                cursor: pointer;
                                -webkit-transition: all 0.25s linear;

                                &.eng{
                                    padding-left: 6vw;
                                }
                            }

                            div.check{
                                display: block;
                                position: absolute;
                                border: 0.2vw solid #aaa;
                                border-radius: 100%;
                                height: 3vw;
                                width: 3vw;
                                top: 0;
                                bottom: 0;
                                margin: auto;
                                right: 1.5vw;
                                z-index: 5;
                                transition: border .25s linear;
                                -webkit-transition: border .25s linear;
                                &::before{
                                    display: block;
                                    position: absolute;
                                    content: '';
                                    border-radius: 100%;
                                    height: 2vw;
                                    width: 2vw;
                                    top: 0;
                                    left: 0;
                                    bottom: 0;
                                    right: 0;
                                    margin: auto;
                                    transition: background 0.25s linear;
                                    -webkit-transition: background 0.25s linear;
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                }

                                &.eng{
                                    left: 1.5vw;
                                    right: auto;
                                }
                            }

                            &:hover{
                                label{
                                    //									color: $active;
                                }
                                div.check{
                                    //                                    border-color: $active;
                                }
                            }
                        }
                        &.answers{
                            li{
                                &:hover{
                                    label{
                                        //                                        color: $inactive;

                                    }
                                    div.check{
                                        border-color: $inactive;
                                    }
                                }
                                input[type=radio]{
                                    &:checked{
                                        & ~ div.check{
                                            border-color: 0.2vw solid #aaa;
                                            &:before{
                                                background-color: rgba(0,0,0,0);
                                            }
                                        }
                                        & ~ label{
                                            color: $inactive;
                                        }
                                    }
                                }
                                &.wrong{
                                    color: $wrong !important;
                                    label{
                                        color: $wrong !important;
                                    }
                                    div.check{
                                        border-color: $wrong !important;
                                        &::before{
                                            font-weight: normal;
                                            background-color: rgba(0,0,0,0) !important;
                                            font-family: 'icomoon' !important;
                                            content: "\1f319";
                                            font-size: 1.7vw;
                                            color: $wrong !important;
                                            display: flex;
                                            justify-content: center;
                                            align-items: center;
                                        }
                                    }
                                }
                                &.right{
                                    color: $checked !important;
                                    label{
                                        color: $checked !important;
                                    }
                                    div.check{
                                        border-color: $checked !important;
                                        &::before{
                                            font-weight: normal;
                                            background-color: rgba(0,0,0,0) !important;
                                            font-family: 'icomoon' !important;
                                            content: "\1f332" !important;
                                            color: $checked !important;
                                            font-size: 1.7vw;
                                            display: flex;
                                            justify-content: center;
                                            align-items: center;
                                        }
                                    }
                                }
                            }
                        }

                        &.eng{
                            li{
                                label{
                                    padding-left: 6vw;
                                }
                                div.check{
                                    left: 1.5vw;
                                    right: auto;
                                }
                            }
                        }
                    }
                }
            }
			
        }

        div#showYourAnswer{
            background-image: url("../images/Finshig_Botton.png");
            z-index : 2 ;
            position: absolute;
            transition-property: width, right, left;
            transition-duration: 1s;
            overflow: hidden;
            background-repeat: no-repeat;
            background-size: cover;
            width: 6.7vw;
            height: 6.7vw;
            top: 46.2%;
            left: 11vw;
            &.active{
                left:4.3vw;
            }
            &.eng{
                left: 81.8vw;
                &.active{
                    left: 88.8vw;
                }
            }
        }
    }

    div.mcq-modal-dialog{

        div.resultcontainer{

            div.result{

                div.borderblackcolore{

                    &.mcq_exit{
                        display: none;
                    }

                    div.circlebottom{

                        div.circlecontainer{
                            display: inline-block;
                            border-radius: 50%;
                            //						position: absolute;
                            bottom: -5vw;
                            width: 13vw;
                            height: 13vw;
                            //						left: 2vw;
                            div.circle{
                                position: relative;
                                width: 100%;
                                height: 100%;
                                div.shadow{
                                    position: absolute;
                                    border-radius: 50%;
                                    top: 6%;
                                    left: -5%;
                                    width: 96%;
                                    height: 98%;
                                    z-index: 0;
                                    background-color: rgba(0, 0, 0, 0.2);
                                }
                                div.innercircle{
                                    padding: 0.5vw;
                                    position: relative;
                                    z-index: 1;
                                    border: 0.3vw solid white;
                                    border-radius: 50%;
                                    width: 100%;
                                    height: 100%;
                                    div.symbolecontainer{
                                        position: absolute;
                                        top: 9%;
                                        left: 6%;
                                        &:nth-child(2){
                                            top: auto;
                                            left: auto;
                                            transform: rotateZ(394deg) scale(0.8);
                                            right: 30%;
                                            bottom: -36%;
                                            div.symbole{
                                                div.circle{
                                                    top: 82%;
                                                    left: 50%;
                                                }
                                            }
                                            div.smallcircle{
                                                &:nth-child(1){
                                                    left: 51%;
                                                    top: 19.2%;
                                                    width: 0.9vw;
                                                    height: 0.9vw;
                                                }
                                                &:nth-child(2){
                                                    left: 21%;
                                                    top: 31%;
                                                    width: 0.8vw;
                                                    height: 0.8vw;
                                                }
                                            }
                                        }
                                        &:nth-child(3){
                                            top: -5%;
                                            left: auto;
                                            right: 17%;
                                            transform-origin: center center;
                                            transform: rotateZ(88deg);
                                        }
                                        div.smallcircle{
                                            border-radius: 50%;
                                            width: 0.8vw;
                                            height: 0.8vw;
                                            background-color: white;
                                            position: absolute;
                                            z-index: 5;
                                            &:nth-child(1){
                                                left: 53%;
                                                top: 11.2%;
                                                width: 0.9vw;
                                                height: 0.9vw;
                                            }
                                            &:nth-child(2){
                                                left: 14%;
                                                top: 32%;
                                                width: 0.8vw;
                                                height: 0.8vw;
                                            }
                                            &:nth-child(3){
                                                left: 53%;
                                                top: 76.2%;
                                                width: 0.9vw;
                                                height: 0.9vw;
                                            }
                                            &:nth-child(4){
                                                left: 14%;
                                                top: 32%;
                                                width: 0.8vw;
                                                height: 0.8vw;
                                            }
                                        }
                                        div.symbole{
                                            z-index: 4;
                                            position: relative;
                                            width: 5vw;
                                            height: 8vw;
                                            overflow: hidden;
                                            top: -5vw;
                                            right: 2vw;
                                            &:nth-child(5){
                                                top: 0;
                                            }
                                            div.circle{
                                                width: 7vw;
                                                height: 6vw;
                                                border: 0.9vw solid white;
                                                border-radius: 50%;
                                                position: absolute;
                                                top: 75%;
                                                left: 50%;
                                                transform-origin: center center;
                                                transform: rotateZ(29deg);
                                            }
                                        }
                                    }
                                    div.inner{
                                        background: linear-gradient(20deg, #f7c61c 50%, #f5d431  50%, #f5d431   100%);
                                        border-radius: 50%;
                                        width: 100%;
                                        height: 100%;
                                        display: flex;
                                        justify-content: center;
                                        align-items: center;
                                        p{
                                            font-size: 2.5vw;
                                            margin: 0;
                                            text-align: center;
                                            font-weight: bold;
                                            width: 100%;
                                        }
                                    }
                                }
                            }
                            &:nth-child(4){
                                left: auto;
                                right: 2vw;
                                div.circle{
                                    div.shadow{
                                        div.innercircle{
                                            div.inner{
                                                p{
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

            }
        }
    }

}


