/* aspect ration : 1.7  (1024*600) BlackBerry Play & Windows Phone */
@media screen and (min-aspect-ratio: 17/10 ) {
	div.subject {
		background-image: url("../images/17-10/index.jpg");
		div.index-title{
			width: 48.82%;
			top: 16.9%;
			left: 32.51%;
			p{
				font-size: 2vw;
			}

			&.soc{
				top: 4.567%;
			}
		}
		div.index-area{
			width: 48.82%;
			height: 61.33%;
			top: 25%;
			left: 32.51%;
			div.unit{
				div.unit-btn{
					height: 11vh;
					div.center-unit{
						p{

						}
					}

					img{
						top: 0%;
						right: 0%;
						height: 90%;
					}
				}
			}
			div.lesson-container{
				div.lesson{
					height: 7.6vh;
				}
			}
		}
	}
}