div.payment{
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	background-size:100% 100%;
	background-position:center;
	font-family: jf;
	background-color: #8cc640;
	background-image: url("../images/Gray.svg");
	background-repeat: repeat;
	background-size: 33vw 10.7vw;

	div.master_header{
		//		position: absolute;
		div.master_left{
			div.banner{
				background-image: url('');
				img.back_matser{
					width: 2vw;
				}
			}
		}
	}

	div.center{
		//		background-color: #ed237c;
		width: 87vw;
		margin: 0 auto;
		height: 87vh;
		border-radius: 3vw;
		padding: 1vw;
		direction: rtl;
		div.dash_line{
			position: relative;
			//			border: 0.2vw dashed white;
			width: 100%;
			height: 100%;
			border-radius: 3vw;
			padding: 1vw;
			background-image: url("../images/White_for_Magenta_yellow.svg");
			background-size: 33vw 10.7vw;
			p.head{
				color: white;
				font-size: 3vh;
				font-weight: bold;
				margin: 0.5vw;
			}
			//
			//			div.center_info_container{
			//				position: absolute;
			//				width: 77%;
			//				height: 95%;
			//				top:0;
			//				left: 0;
			//				right: 0;
			//				bottom: -24vh;
			//				margin: auto;
			//				border-radius: 3vw;
			div.shadow{
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				margin: auto;
				position: absolute;
				z-index: 1;
				width: 100%;
				height: 60%;
				top: 30%;
				top: 5%;
				border-radius: 3vw;
				box-shadow: 0px 10px 23px 5px rgba(0,0,0,0.2);
			}
			div.center_info{
				background-color: #f1f1f1;
				position: absolute;
				width: 100%;
				height: 100%;
				top:0;
				left: 0;
				border-radius: 3vw;
				z-index: 2;

				div.center_image{
					position: relative;
					margin: 0 auto;
					top: -8vh;
					width: 23vh;
					height: 23vh;
					border-radius: 50%;
					padding: 0.3vw;
					img{
						width: 100%;
					}
				}

				div.profile_container{
					position: absolute;
					width: 100%;
					top: 0;
					padding: 20vh 0 0;
					height: 100%;
					text-align: center;
					color: black;
					font-weight: bold;

					div.logo{
						margin-bottom: 1vh;
						img{
							&:first-child{
								height: 15vh;
								display: block;
								margin: 0 auto;
							}
							&:last-child{
								height: 10vh;
								display: block;
								margin: 0 auto;
							}
						}
					}
					p{
						font-size: 1.8vh;;
						&.pink{
							font-size: 2.2vh;
							color: #c71d68;
						}
					}
				}
			}
			//			}
		}
	}
}
@import "payment/4-3.scss";
@import "payment/3-2.scss";
@import "payment/16-10.scss";
@import "payment/17-10.scss";
@import "payment/16-9.scss";