div.exam{
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;

	div.master_background{
		background: linear-gradient(24deg, rgb(247, 146, 28), rgb(242, 94, 8));
		background: -webkit-linear-gradient(24deg, rgb(247, 146, 28), rgb(242, 94, 8));

	}


	div.master_content{
		div.content_square{
			border: 0.5vw solid #ea7202;
//			overflow-y: auto;
			div.exam_container{
				position: relative;
				overflow-y: auto;
				margin-top: 24px;
				height: 100%;

				div.EContainer{
					direction: rtl;
					display: block;

					.examHeader{
						text-align: center;
						font-weight: bold;
						font-size: 2.5vw;
						//				font-size: 20px;
					}

					&.eng{
						direction: ltr;
					}

					.exam-questionanswer{
						margin: 3vw 0;
						display: flex;
						flex-direction: column;

						.answer_exam{
							float:left;
							display:none;
							margin: 2vw 0;

							&.answer_exam.alert-danger{
								color: #fefefe;
								background-color: #fefefe;
								border-color: #f4dbdf;
							}
						}

						.answerBtnDiv{
							text-align: center;
							margin: 2vw;
						}

						.resize_Imgwidth{
							width: 100%;
						}

					}
				}
			}
		}

	}
}
