div.select{
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;

	div.master_header {
		position: absolute;
	}


	div.background{
		position: absolute;
		width: 100%;
		height: 100%;
		background: radial-gradient(rgb(130, 95, 153), rgb(107, 99, 185));
		z-index: 1;
		overflow: hidden;

		div.circle{
			position: absolute;
			border-radius: 50%;

			background-color: #e1e1e1;
			opacity: 0.1;
			background: url('../images/circle.svg');
			background-repeat: no-repeat;
			-webkit-touch-callout: none; /* iOS Safari */
			-webkit-user-select: none;   /* Chrome/Safari/Opera */
			-khtml-user-select: none;    /* Konqueror */
			-moz-user-select: none;      /* Firefox */
			-ms-user-select: none;       /* Internet Explorer/Edge */
			user-select: none; 
			-ms-touch-select : none;
			outline-style : none;

			&:nth-child(1){
				background: linear-gradient(46deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.3));
				width: 78vw;
				//				height: 37vw;
				height: 50vh;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				opacity: 0.7;
			}
			&:nth-child(2){
				top: 77%;
				left: -10%;
				width: 31vh;
				height: 31vh;
				display: none;
			}
			&:nth-child(3){
				top: -19%;
				left: 13vh;
				width: 31vh;
				height: 31vh;
				display: none;
			}
			&:nth-child(4){
				top: 6%;
				left: 9%;
				width: 15vh;
				height: 15vh;
			}
			&:nth-child(5){
				top: 7vh;
				left: 102vh;
				width: 8vh;
				height: 8vh;
			}
			&:nth-child(6){
				top: -14%;
				right: -10%;
				width: 35vh;
				height: 28vh;
				//display: none;
			}
			&:nth-child(7){
				top: 34%;
				left: 92%;
				width: 13vh;
				height: 13vh;
				margin: 0;
			}
			&:nth-child(8){
				top: 48vh;
				right: -18%;
				width: 24vh;
				height: 24vh;
			}
			&:nth-child(9){
				top: 76%;
				right: 9vw;
				width: 16vw;
				height: 17vw;
				display: none;
			}
		}

		img.logo{
			position: absolute;
			margin: 0;
			width: 20vw;
			bottom: 3vh;
			left: 2vw;
		}

		img.BoyGirl_logo{
			position: absolute;
			margin: 0;
			height: 24vh;
			bottom: 0vh;
			right: 1vw;
		}

		div.footer{
			position: absolute;
			bottom: 0.5vw;
			width: 100%;
			z-index: 2;
			color: white;

			div.text_footer{
				//				position: absolute;
				bottom: 0;
				width: 100%;
				p{
					margin: 0;
					text-align: center;
					font-size: 0.9vw;
					font-weight: bold;
					font-family: jf;
				}
			}

		}
	}

	div.content{
		position: relative;
		width: 100%;
		height: 100%;
		z-index: 2;
		display: flex;

		div.select-title{
			position: absolute;
			width: auto;
			height: auto;
			left: 0;
			right: 0;
			top: 8%;
			display: flex;
			justify-content: center;
			align-items: center;
			direction: rtl;
			font-size: 2.3vw;
			p{
				color: white;
				text-align: center;
				direction: rtl;
				margin: 0;
			}

			span{
				color: yellow;
				direction: ltr;
				margin-right: 1vw;
			}
		}

		div.Qbank{
