/* aspect ration : 1.8  (1280×720) or (640*360)  BlackBerry Z30 - Galaxy Note  */
@media screen and (min-aspect-ratio : 16/9 ) {
	div.subject {
		background-image: url("../images/16-9/index.jpg");
		div.index-title{
			width: 48.75%;
			top: 16.35%;
			left: 32.57%;
			p{
				font-size: 2vw;
			}

			&.soc{
				top: 7.567%;
			}
		}
		div.index-area{
			width: 48.75%;
			height: 64.50%;
			top: 24.75%;
			left: 32.57%;
			div.unit{
				div.unit-btn{
					height: 11vh;
					div.center-unit{
						p{

						}
					}

					img{
						position: absolute;
						top: 0%;
						right: 0%;
						height: 90%;
					}
				}
			}
			div.lesson-container{
				div.lesson{
					height: 7.6vh;
					p{
						padding: 1vw 0;
					}
				}
			}
		}
	}
}