/* aspect ration : 1.6  (1280*800) Google Nexus 10 */
@media screen and (min-aspect-ratio: 16/10 ) {
	div.subject {
		background-image: url("../images/16-10/index.jpg");
		div.index-title{
			width: 48.75%;
			top: 17.15%;
			left: 32.57%;
			p{
				font-size: 2.8vw;
			}
			&.soc{
				top: 3.567%;
			}
		}
		div.index-area{
			width: 48.75%;
			height: 64.50%;
			top: 24.75%;
			left: 32.57%;
			div.unit{
				div.unit-btn{
					height: 11vh;
					div.center-unit{
						p{

						}
					}

					img{
						top: 2%;
						right: -0.4%;
						height: 90%;
					}
				}
			}
			div.lesson-container{
				div.lesson{
					height: 7vh;
				}
			}
		}
	}
}