#canvas-container{
	position: absolute;
	height: 0;
	width: 0;
	overflow-x: hidden;
	overflow-y: auto;
	z-index: 3;
	pointer-events: none;
	#iframe-canvas{
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0.3;
		display: block;
	}
	#iframe-canvas-pen{
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		opacity: 1;
	}
}

#canvas-container, section {
	-ms-overflow-style: -ms-autohiding-scrollbar !important;
	-ms-scroll-chaining: none;
}
