div.login{
	touch-action: none;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	position: relative;
	//	transform: translateX(0);
	//	transition: 1s transform;

	div.background{
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: #eac52e;
		z-index: 1;


		img.star{
			width: 15%;
			position: absolute;
			z-index: 1;

			&:nth-child(1){
				top: 10%;
				left: -3%;
			}
			&:nth-child(2){
				top: 42%;
				left: 2%;
				width: 4%;
			}
			&:nth-child(3){
				top: 67%;
				left: 7%;
				width: 13%;
			}
			&:nth-child(4){
				top: 8%;
				left: 44%;
				width: 12%;
			}
			&:nth-child(5){
				top: -2%;
				left: 67%;
				width: 6%;
			}
			&:nth-child(6){
				top: 5%;
				right: 1%;
				width: 9%;
			}
			&:nth-child(7){
				top: 28%;
				right: 4%;
				width: 7%;
			}
			&:nth-child(8){
				bottom: 8%;
				right: 2%;
				width: 23%;
			}

		}

		div.pen_footer{
			position: absolute;
			bottom: 0;
			width: 100%;
			z-index: 2;
			img{
				width: 100%;
			}

			div.text_footer{
				position: absolute;
				bottom: 0.5vw;
				width: 55%;
				width: 68%;
				p{
					margin: 0;
					text-align: center;
					font-size: 0.9vw;
					font-weight: bold;
					font-family: jf;
					height: 1.1vw;
				}
			}

		}
	}

	div.content{
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: 2;
		display: flex;
		overflow: hidden;

		&.unregistered{
			div.left{
				transform: translateX(0);
			}
			div.right{
				transform: translateX(0);
			}
		}

		div.left{
			width: 50%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			transform: translateX(-100%);
			transition: 3s;

			div.login_container{
				width: 80%;
				height: 40%;
				display: flex;
				justify-content: space-between;
				align-items: center;
				flex-direction: column;

				img{
					height: 28%;
				}

			}
		}

		div.right{
			width: 50%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			transform: translateX(-50%);
			transition: 3s;
			img.logo{
				width: 70%;
				//				height: 22vh;
			}

			div.character_star{
				position: relative;
				img.character{
					height: 48vh;
					position: relative;
					z-index: 2;
				}
				img.flower{
					width: 11%;
					position: absolute;
					z-index: 1;
					&:nth-child(2){
						top: -4%;
						left: -3%;

					}
					&:nth-child(3){
						top: 10%;
						left: -7%;

					}
					&:nth-child(4){
						top: 30%;
						left: -7%;
						width: 6%;
					}
					&:nth-child(5){
						top: -7%;
						left: 11%;
						//						width: ;
					}
					&:nth-child(6){
						top: 12%;
						left: 7%;
						width: 3%;
					}
					&:nth-child(7){
						top: 23%;
						left: 2%;
						width: 7%;
					}
					&:nth-child(8){
						top: 34%;
						left: 2%;
						width: 5%;
					}
					&:nth-child(9){
						top: -7%;
						right: 16%;
						width: 7%;
					}
					&:nth-child(10){
						top: 0%;
						right: 5%;
						width: 6% ;
					}
					&:nth-child(11){
						top: 12%;
						right: 8%;
						width: 6%;
					}
					&:nth-child(12){
						top: 9%;
						right: -3%;
						width: 8%;
					}
					&:nth-child(13){
						top: 29%;
						right: 1%;
						width: 8%;
					}
					&:nth-child(14){
						top: 23%;
						right: 5%;
						width: 2%;
					}
				}
			}
		}
	}

}
