div.profile{
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	background-repeat:no-repeat;
	background-size:100% 100%;
	background-position:center;
	background-color: #bebec0;
	font-family: jf;
	background-image: url("../images/Gray.svg");
	background-repeat: repeat;
	background-size: 33vw 10.7vw;

	div.master_header{
		//		position: absolute;
		div.master_left{
			div.banner{
				background-image: url('');
				img.back_matser{
					width: 2vw;
				}
				img.menu_edit{
					width: 3vw;
					margin-left: 2vw;
				}
				img.menu_edit_hidden{
					display: none;
					width: 3vw;
					margin-left: 2vw;
				}
			}
		}
	}

	div.center{
		background-color: #fdc026;
		width: 70vw;
		margin: 0 auto;
		height: 71vh;
		border-radius: 3vw;
		padding: 1vw;
		direction: rtl;
		div.dash_line{
			position: relative;
			border: 0.2vw dashed white;
			width: 100%;
			height: 100%;
			border-radius: 3vw;
			padding: 1vw;
			background-image: url("../images/White_for_Magenta_yellow.svg");
			background-size: 33vw 10.7vw;
			p.head{
				color: white;
				font-size: 3vh;
				font-weight: bold;
				margin: 0.5vw;
			}

			div.center_info_container{
				position: absolute;
				width: 77%;
				height: 100%;
				top:0;
				left: 0;
				right: 0;
				bottom: -32vh;
				margin: auto;
				border-radius: 3vw;
				div.shadow{
					position: absolute;
					z-index: 1;
					width: 100%;
					height: 60%;
					top: 30%;
					top: 5%;
					border-radius: 3vw;
					box-shadow: 0px 10px 23px 5px rgba(0,0,0,0.2);
				}
				div.center_info{
					background-color: #f1f1f1;
					position: absolute;
					width: 100%;
					height: 100%;
					top:0;
					left: 0;
					border-radius: 3vw;
					z-index: 2;

					div.center_image{
						position: relative;
						margin: 0 auto;
						top: -13vh;
						width: 23vh;
						height: 23vh;
						border-radius: 50%;
						padding: 0.3vw;
						background-color: #179dd9;
						div.dashed{
							border: 0.2vw dashed white;
							border-radius: 50%;
							padding: 0.3vw;
							width: 100%;
							height: 100%;
							div.innercircle{
								overflow: hidden;
								border-radius: 50%;
								width: 100%;
								height: 100%;
								background-color: #6dccd9;
								//								padding: 2vh;
								background-size: contain;
								display: flex;
								align-items: center;
								justify-content: center;
								div.imagecircle{
									border-radius: 50%;
									width: 90%;
									height: 90%;
									overflow: hidden;
									background-size: cover;
								}
							}
						}
					}

					div.profile_container{
						position: absolute;
						width: 100%;
						top: 0;
						padding: 9.2vh 5vw 0;
						height: 100%;
						text-align: center;


						h1, h4{
							margin: 0;
							color: #bf1e2e;
							font-weight: bold;
						}

						h1{
							font-size: 3.5vh;
							margin-top: 2vh;
							margin-bottom: 4vh;
						}

						h4{
							font-size: 2.5vh;
							margin: 0;
							//							margin-top: 2vh;
							//							margin-bottom: 2vh;
						}

						div._info{
							border-bottom: 0.2vw solid #cacbcc;
							height: 18%;
							margin: 2vh;
							p{
								margin: 1vh 0;
							}
							&:last-child{
								border-bottom:none;
							}

						}

						.base{
							display: block;
						}

						._edit{
							display: none;
							text-align: center;
							margin: 1vh auto;
							direction: rtl;
							//							border-radius: 3vh;
							//							font-size: 2.4vh;
							&.name{
								margin-top: 1vh;
								font-size: 3.5vh;
								color: #bf1e2e;
								border-radius: 1.5vw;
								direction: rtl;
							}
							//							&.stage2{
							//								padding: 0vw 2vw;
							//							}


						}

						input{
							direction: ltr;
						}
					}
				}
			}
		}
	}


}
@import "profile/4-3.scss";
@import "profile/3-2.scss";
@import "profile/16-10.scss";
@import "profile/17-10.scss";
@import "profile/16-9.scss";