div.try{
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;

	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;

	div.master_background{
		background-color: #f7c71d;
	}

	div.master_content{
		div.content_square{
			border: 0.5vw solid #ea7202;
			div.iframe-try{
				//				position: relative;
				overflow: hidden;

				iframe.Try-resize{

					height: 573px;
					width: 990px;
					border: 0px none;
					transform-origin: left top 0px;
					transform: scale(1);
				}

				img{
					//			top: 261px;
					top: 0;
					bottom: 0;
					right: 0;
					left: 0;
					margin: auto;
					position: absolute;
				}
			}
		}
	}
}
