div#VideoDiv div.modal-dialog{
	width: 100%;
	height: 100%;
	margin: 0;
}

div#VideoDiv div.modal-content{
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	border: 0px solid rgba(0, 0, 0, 0.5);
	border-radius: 0;
}

div#VideoDiv div.modal-body{
	width: 100%;
	height: 100%;
}
div#VideoDiv div.framediv{
	width: 100%;
	height: 100%;
}

div#VideoDiv div.modal-content div.modal-body button.close{
	font-size: 2.7vw;
	background-color: rgba(255,255,255,.6);
	padding: 0px 10px;
}

div#VideoDiv iframe#Myiframe{
	height: 95%;
	width: 100%;
	border: 0px solid rgba(0,0,0,0.5);
	background-color: black;
}


div#VideoDiv div#videoModal{
	padding: 0;
}

div#VideoDiv div.video-btn{
	margin-top: .5%;
	margin-bottom: .5%;
	text-align: center;
}

div#VideoDiv div.video-btn a{
	display: inline-block;
}

div#VideoDiv .modal-open .modal{
	overflow: visible;
}

div#VideoDiv ul#lightSlider{
	height: auto;
	margin: 0 auto;
}

div#VideoDiv ul#lightSlider li img{
	width: 100%;
} 

div#VideoDiv .lSSlideOuter{
	margin-top: 15%;
	direction: ltr;
}

