div.video{

	position: absolute;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;

	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;
	overflow: hidden;

	div.master_background{
		background: linear-gradient(55deg, rgb(247, 199, 29), rgb(240, 134, 16));

		div.circle{
			position: absolute;
			border-radius: 50%;
			background-color: white;
			background: #fadd29;
			opacity: 0.5;

			&:nth-child(1){
				background: #fadd29;
				width: 18vw;
				height: 18vw;
				left: -10vw;
				top: 2vw;
				margin: auto;
			}
			&:nth-child(2){
				width: 17vw;
				height: 17vw;
				left: 1vw;
				top: 32vw;
				margin: auto;
			}
			&:nth-child(3){
				top: -22%;
				left: 27%;
				width: 27vh;
				height: 27vh;
				display: none;
			}
			&:nth-child(4){
				top: 10%;
				left: 63vh;
				width: 16vh;
				height: 15vh;
				display: none;
			}
			&:nth-child(5){
				top: 7vh;
				left: 102vh;
				width: 8vh;
				height: 8vh;
			}
			&:nth-child(6){
				top: -17%;
				right: -12%;
				width: 35vh;
				height: 28vh;
			}
			&:nth-child(7){
				top: 22vh;
				right: 4vh;
				width: 16vh;
				height: 15vh;
				display: none;
			}
			&:nth-child(8){
				top: 19vw;
				right: -8vw;
				width: 14vw;
				height: 14vw;
			}
			&:nth-child(9){
				top: 71%;
				right: 12%;
				width: 24vh;
				height: 24vh;
				display: none;
			}
			&:nth-child(10){
				bottom: -2vw;
				right: 24vw;
				width: 8vw;
				height: 8vw;
				opacity: 0.4;
				margin: auto;
			}
			&:nth-child(11){
				bottom: 8vh;
				left: 41vh;
				width: 7vh;
				height: 7vh;
				opacity: 0.4;
			}
		}

		img.logo{
			position: absolute;
			margin: 0;
			width: 20vw;
			bottom: 3vh;
			left: 2vw;
		}

		img.logo_Girl{
			position: absolute;
			margin: 0;
			height: 32vh;
			bottom: 0vh;
			right: 1vw;
		}

		div.footer{
			position: absolute;
			bottom: 0.5vw;
			width: 100%;
			z-index: 2;
			color: white;

			div.text_footer{
				position: absolute;
				bottom: 0;
				width: 100%;
				p{
					margin: 0;
					text-align: center;
					font-size: 0.9vw;
					font-weight: bold;
					font-family: jf;
				}
			}

		}
	}

	div.video_show{
		position: absolute;
		display: flex;
		align-items: center;
		width: 77.4%;
		height: 71.6%;
		top: 12.8%;
		left: 10.8%;
		overflow-y: auto;
		direction: rtl;
		div.mySlider{
			height: 48vh;
			margin-bottom: 4vw;
			width: 90%;
			margin: 0 auto;
			direction: ltr;
			div.slick-list{
				padding: 0px 8vw !important;
				padding-top: 20px !important;
			}
			button.slick-arrow{
				&:before{
					font-size: 3vw;
				}
			}
			div.slick-track{
				height: 40vh;
			}
			div.video-slide{
				display: flex;
				align-items: center;
				justify-content: space-around;
				position: relative;
				a{
					p{
						font-size: 2.1vw;
					}
					img.video-src{

						//						width: 95%;
						margin: 0 auto;

					}
					img.video-thambanils{
						top: 0;
						bottom: 0;
						left: 0;
						right: 0;
						position: absolute;
						margin: auto;
						opacity: 0.8;

					}
					//					img{
					//
					//					}
				}
			}
			.slick-dots{
				li{
					button{
						width: 4vw;
						height: 4vw;
						//						padding: 1vw;
						margin: auto;
						&:before{
							font-size: 1.5vw;
							line-height: 4vw;
							width: 4vw;
							height: 4vw;
						}
					}
				}
			}
		}
	}
}
