div.store{
	touch-action: none;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	position: absolute;
	top: 0;
	//    background-repeat:no-repeat;
	//    background-size:100% 100%;
	//    background-position:center;

	//	img.subscribe{
	//		display: none;
	//		z-index: 2;
	//		width: 17%;
	//		top: 1vh;
	//		left: 1vh;
	//	}



	div.master_header{
		width: 100%;
		z-index: 2;
		position: absolute;
		display: flex;
		flex-direction: row-reverse;
		align-items: center;
		justify-content: space-between;
		right: 0;
		z-index: 3;
		top: 0;

		div.master_right{
			width: 39%;
			height: 100%;
			position: relative;
			direction: rtl;
			display: flex;

			div.right{
				width: 50%;
				justify-content: space-around;
				display: flex;
				align-items: center;
				img.menuu{
					width: 4.5vw;
				}
				div.right_vLine{
					height: 2.9vw;
					width: 0.3vw;
					background-color: white;

				}
				img.storeIcon{
					width: 5.5vw;
					//                    margin-left: 4vw;
					margin-top: 1vw;					
				}
				img.helpIcon{
					width: 5.5vw;
					//margin-left: 4vw;
					margin-top: 1vw;
					transform: scaleX(-1);
				}
			}

			div.left{
				width: 49%;
				//				direction: ltr;
				justify-content: flex-end;
				display: flex;
				align-items: center;
				div.left_vLine{
					height: 2.9vw;
					width: 0.3vw;
					background-color: white;
					margin-left: 0.4vw;
				}

				img{
					width: 4.4vw;
				}
			}
		}

		div.master_left{
			width: 61%;
			height: 100%;

			div.bigredbutton{
				display: none;
				width: 18vw;
				height: 5vw;
				background-color: red;
				border-radius: 5vw;
				padding: .5vw;
				margin-top: 0.5vw;

				div.redbutton{
					width: 100%;
					height: 100%;
					border-radius: 5vw;
					display: flex;
					border: .3vw dashed white;
					justify-content: center;
					align-items: center;
					p{
						text-align: center;
						color: white;
						font-size: 2.5vw;
						margin: 0;
					}
				}
			}
		}

	}

	div.content{
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: 2;
		display: flex;

		div.left{
			background: white;
			width: 61%;
			height: 100%;
			display: flex;
			justify-content: flex-end;
			align-items: center;
			position: relative;
			overflow: hidden;
			direction: rtl;

			div.circle_container{
				//				position: relative;
				//				right: 0vh;
				//				top: 18vh;
				//				height: 85%;
				position: absolute;
				right: 0vh;
				top: 10vh;
				width: auto;

				div.circle_Div{
					width: 20vh;
					height: 20vh;
					position: relative;

					&:nth-child(1){

						div.circle{
							background-color: #0b73a6;
							p{
								padding-bottom: 5vw;
								padding-left: 2vw;
							}
						}

						right:-6.7vh;
						top: -0.5vh;
					}

					&:nth-child(2){
						right: -4vh;
						top: -10vh;
						div.circle{
							background-color: #dda50d;
							p{
								padding-left: 4vw;
							}
						}
					}

					&:nth-child(3){
						div.circle{
							background-color: #4c45a1;
							p{
								padding-left: 3vw;
							}
						}
						right: -4.2vh;
						top: -16vh;
						z-index: 1;
					}

					&:nth-child(4){
						div.circle{
							background-color: #d365af;
							p{
								padding-left: 3vw;
							}
						}
						right: -7vh;
						top: -22vh;
					}

					&:nth-child(5){
						div.circle{
							background-color: #5a8e0f;
							p{
								padding-top: 2vw;
								padding-left: 4vw;
							}
						}
						right: -2vh;
						top: -31vh;
						z-index: 1;
					}

					&:nth-child(6){
						div.circle{
							background-color: #0870a2;
							p{
								padding-left: 3vw;
							}
						}
						right: -6vh;
						top: -36vh;
						z-index: 0;
					}

					div.circle{
						display: flex;
						justify-content: center;
						align-items: center;
						position: absolute;
						z-index: 2;
						width: 100%;
						height: 100%;
						border-radius: 50%;
						border: 0.5vw solid white;

						p{
							direction: rtl;
							font-size: 2.3vh;
							text-align: center;
							color: white;
							margin: 0;
							//							width: 7vw;
							font-family: jf;
							padding-right: 2.2vh;
							width: 0vw;
						}

					}
					div.shadow{
						position: absolute;
						z-index: 1;
						background-color: rgba(0, 0, 0, 0.1);
						width: 100%;
						height: 100%;
						border-radius: 50%;
						top: 5%;
						left: -5%;
					}
				}
			}


			div.book-container{
				position: absolute;
				left: 0;
				top: 10vh;
				width: 50vw;
				height: 88vh;
				overflow-y: scroll;
				display: flex;
				flex-direction: column;
				justify-content: space-around;
				align-items: center;
				flex-wrap: wrap;
				flex-direction: row;
				div.row{
					display: flex;
					width: 100%;
					justify-content: space-around;
					align-items: center;
					height: 25vh;
					>div{
						width: 40%;
						display: flex;
						flex-direction: column;
						justify-content: center;
						position: relative;
						height: 100%;
						//						background: url("../images/loading_spinner.gif") 60% 60% no-repeat;
						background-size: 60%;
						align-items: center;

						img{
							width: auto;
							height: 80%;
							margin-bottom: 0.1vw;
						}
						.loading{
							//background-image: url("../images/loading_spinner.gif");
						}
						.subjectTitle{
							height: 20%;
							font-size: 1.8vw;
							font-family: jf;
							display: flex;
							align-items: center;
							justify-content: center;
							color: white;
							border-radius: 0.5vw;
							background-color: #686868;
							padding: 0.2vw 0.6vw;
							width: 13.5vw;
						}
						.SocTitle{
							width: 14.5vw;
						}
						div.subscribe-icon{
							background-image: url("../images/tick.png");
							width: 3.5vw;
							height: 3.5vw;
							background-size: 100%;
							background-position: center center;
							position: absolute;
							//					bottom: 50px;
							//					right: 0;
						}
					}

				}
			}
		}

		div.right{
			width: 39%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			position: relative;
			box-shadow: -5px 0px 40px 0px rgba(0,0,0,0.5);
			z-index: 2;

			&.first{
				background: linear-gradient(0deg, rgba(59,105,174,1) 0%, rgba(76,134,223,1) 25%, rgba(82,146,242,1) 50%, rgba(76,134,223,1) 75%, rgba(59,105,174,1) 100%);
			}
			&.second{
				background: linear-gradient(0deg, rgba(167,125,36,1) 0%, rgba(214,160,46,1) 25%, rgba(214,160,46,1) 50%, rgba(214,160,46,1) 75%, rgba(167,125,36,1) 100%);
			}
			&.third{
				background: linear-gradient(0deg, rgba(93,90,139,1) 0%, rgba(119,116,178,1) 25%, rgba(119,116,178,1) 50%, rgba(119,116,178,1) 75%, rgba(93,90,139,1) 100%);
			}
			&.fourth{
				background: linear-gradient(0deg, rgba(190,92,131,1) 0%, rgba(241,117,167,1) 25%, rgba(241,117,167,1) 50%, rgba(241,117,167,1) 75%, rgba(190,92,131,1) 100%);
			}
			&.fifth{
				background: linear-gradient(0deg, rgba(118,149,40,1) 0%, rgba(152,192,52,1) 25%, rgba(152,192,52,1) 50%, rgba(152,192,52,1) 75%, rgba(118,149,40,1) 99%, rgba(118,149,40,1) 100%);
			}
			&.sixth{
				background: linear-gradient(0deg, rgba(38,134,187,1) 0%, rgba(49,169,237,1) 25%, rgba(49,169,237,1) 50%, rgba(49,169,237,1) 75%, rgba(38,134,187,1) 99%, rgba(38,134,187,1) 100%);
			}


			div.stage{
				margin: 11vh 0 0;
				p{
					text-align: center;
					font-family: jf;
					font-size: 4vw;
					font-weight: lighter;
					color: white;
					margin: 0;
				}
			}

			img.logo{
				width:60%;
				//				height: 22vh;
			}

			div.character_star{
				position: relative;
				padding: 2vh;
				padding-bottom: 0;
				img.character{
					height: 48vh;
					position: relative;
					z-index: 2;
				}
				img.flower{
					width: 11%;
					position: absolute;
					z-index: 1;
					&:nth-child(2){
						top: 0;
						left: 8%;

					}
					&:nth-child(3){
						top: 15%;
						left: 7%;

					}
					&:nth-child(4){
						top: 29%;
						left: 9%;
						width: 6%;
					}
					&:nth-child(5){
						top: 1%;
						left: 22%;
					}
					&:nth-child(6){
						top: 12%;
						left: 19%;
						width: 3%;
					}
					&:nth-child(7){
						top: 25%;
						left: 16%;
						width: 8%;
					}
					&:nth-child(8){
						top: 35%;
						left: 17%;
						width: 4%;
					}
					&:nth-child(9){
						top: 0%;
						right: 26%;
						width: 8%;
					}
					&:nth-child(10){
						top: 2%;
						right: 15%;
						width: 7%;
					}
					&:nth-child(11){
						top: 14%;
						right: 19%;
						width: 6%;
					}
					&:nth-child(12){
						top: 13%;
						right: 8%;
						width: 9%;
					}
					&:nth-child(13){
						top: 27%;
						right: 13%;
						width: 8%;
					}
					&:nth-child(14){
						top: 22%;
						right: 18%;
						width: 2%;
					}
				}
			}
		}
	}

}


