div.subject{
	touch-action: none;
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	position: absolute;
	top: 0;
	background-repeat: no-repeat;
	background-size: 100% 100%;
	//	-ms-touch-action: auto;
	overflow: hidden;
	-ms-content-zooming: none;

	div.index-title{
		position: absolute;
		p{
			color: white;
			text-align: center;
			font-size: 3vw;
		}
	}

	div.background{
		//		position: absolute;
		//		width: 100%;
		//		height: 100%;
		//		background: radial-gradient(rgb(130, 95, 153), rgb(107, 99, 185));
		//		z-index: 1;
		//		overflow: hidden;



		img.logo{
			position: absolute;
			margin: 0;
			width: 18vw;
			bottom: 0vh;
			left: 2vw;
		}

		img.BoyGirl_logo{
			position: absolute;
			margin: 0;
			height: 24vh;
			bottom: 0vh;
			right: 1vw;
		}

		div.footer{
			position: absolute;
			bottom: 0.5vw;
			width: 100%;
			z-index: 2;
			color: white;

			div.text_footer{
				position: absolute;
				bottom: 0;
				width: 100%;
				p{
					margin: 0;
					text-align: center;
					font-size: 0.9vw;
					font-weight: bold;
					font-family: jf;
				}
			}

		}
	}

	div.index-area {
		*{
			touch-action: pan-y;
		}
		position: absolute;
		overflow-y: auto;
		overflow-x: hidden;
		background: url("../images/spinner.gif");
		background-size: 20%;
		background-repeat: no-repeat;
		background-position: center;
		//		touch-action : cross-slide-y;
		//
		//		-ms-touch-action: auto;
		//		-ms-touch-action: pan-x;
		//		-ms-touch-action: cross-slide-x;
		div.unit{
			width: 100%;
			div.unit-btn{
				position: relative;
				background-image: url(../images/unit_back.png);
				background-repeat: no-repeat;
				background-size: 100% auto;
				background-position: center;
				height: 11vh;
				div.center-unit{
					height: 10vh;
					padding-right: 12%;
					display: flex;
					justify-content: center;
					align-items: center;
					p{
						//						display: flex;
						//						justify-content: center;
						//						align-items: center;
						width: 100%;
						text-align: center;
						margin: 0;
						font-size: 2.4vw;
						font-weight: bold;
						margin: 0 0 0.8vw;
						// Remove direction to match dot direction at Social 6
						// direction: rtl;
						&.eng{
							direction: ltr;
						}
					}
				}

				img{
					position: absolute;
					z-index: 1;
				}
			}
		}
		div.lesson-container{
			div.lesson{
				width: 80%;
				margin: 0 auto;
				background-repeat: no-repeat;
				background-image: url(../images/lesson_back.png);
				background-size: 100% auto;
				opacity: 0.5;
				&.enable{
					opacity: 1;
				}
				p{
					height: 100%;
					margin: 0;
					color: white;
					//					padding: 0;
					padding: 0.8vw 0;
					//					display: flex;
					//					justify-content: center;
					//					align-items: center;

					font-weight: bold;
					font-size: 2vw;
					text-align: center;
				}
			}
		}
	}
}
@import "subject/4-3.scss";
@import "subject/3-2.scss";
@import "subject/16-10.scss";
@import "subject/17-10.scss";
@import "subject/16-9.scss";