div.registration{
	z-index: 1;
	width: 100%;
	height: 100vh;
	margin: 0px;
	//	position: absolute;
	top: 0px;
	//	left: 0px;

	div.background{
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: #07bdf1;
		z-index: 1;


		img.star{
			width: 15%;
			position: absolute;
			z-index: 1;

			&:nth-child(1){
				top: 13%;
				left: -6%;
				width: 17%;
			}
			&:nth-child(2){
				top: 42%;
				left: 12%;
				width: 4%;
			}
			&:nth-child(3){
				top: 72%;
				left: 7%;
				width: 14%;
			}
			&:nth-child(4){
				top: 8%;
				left: 44%;
				width: 12%;
			}
			&:nth-child(5){
				top: -2%;
				left: 67%;
				width: 6%;
			}
			&:nth-child(6){
				top: 5%;
				right: 1%;
				width: 9%;
			}
			&:nth-child(7){
				top: 28%;
				right: 4%;
				width: 7%;
			}
			&:nth-child(8){
				bottom: 8%;
				right: 2%;
				width: 23%;
			}

		}

		div.pen_footer{
			position: absolute;
			bottom: 0;
			width: 100%;
			z-index: 2;
			img{
				width: 100%;
			}

			div.text_footer{
				position: absolute;
				bottom: 0.5vw;
				width: 50%;
				//				width: 68%;
				p{
					margin: 0;
					text-align: center;
					font-size: 0.9vw;
					font-weight: bold;
					font-family: jf;
					height: 1.1vw;
				}
			}

		}
	}

	div.content{
		display: flex;
		width: 100%;
		height: 100%;
		z-index: 2;
		position: relative;

		div.left{
			width:45vw;
			height: 100vh;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			position: relative;
			z-index: 2;



			img.logo{
				width:60%;
				//				height: 22vh;
			}

			div.character_star{
				position: relative;
				padding: 4vh;
				padding-bottom: 0;
				img.character{
					height: 40vh;
					position: relative;
					z-index: 2;
				}
				img.flower{
					width: 11%;
					position: absolute;
					z-index: 1;
					&:nth-child(2){
						top:-5%;
						left: 13%;
						width: 3%;
					}
					&:nth-child(3){
						top: 4%;
						left: 6%;
					}
					&:nth-child(4){
						top: 19%;
						left: 10%;
						width: 3%;
					}
					&:nth-child(5){
						top: 26%;
						left: 6%;
						width: 7%;
					}
					&:nth-child(6){
						top: 40%;
						left: 8%;
						width: 4%;
					}
					&:nth-child(7){
						top: -3%;
						left: 20%;
						width: 8%;
					}
					&:nth-child(8){
						top: 1%;
						left: 31%;
						width: 5%;
					}
					&:nth-child(9){
						top: 0%;
						left: 40%;
						width: 8%;
					}
					&:nth-child(10){
						top: 8%;
						right: 48%;
						width: 4%;
					}
					&:nth-child(11){
						top: 2%;
						right: 42%;
						width: 4%;
					}
					&:nth-child(12){
						top: -2%;
						right: 31%;
						width: 7%;
					}
					&:nth-child(13){
						top: -1%;
						right: 24%;
						width: 4%;
					}
					&:nth-child(14){
						top: -7%;
						right: 10%;
						width: 10%;
					}
					&:nth-child(15){
						top: 43%;
						right: 10%;
						width: 5%;
					}
					&:nth-child(16){
						top: 51%;
						right: 10%;
						width: 8%;
					}
					&:nth-child(17){
						top: 64%;
						right: 13%;
						width: 4%;
					}
				}
			}
		}

		div.right{
			width: 55vw;
			height: 100vh;
			position: relative;
			z-index: 1;
			div.circlescontainer{
				position: absolute;
				top: 0;
				bottom: 0;
				width: 60vw;
				height: 60vw;
				left: -7vw;
				margin: auto;
				div.circle{
					width: 100%;
					height: 100%;
					border-radius: 50%;
					padding: 1vw;
					&.one{
						border:1vw solid rgba(102, 214, 246, 0.2);
					}
					&.two{
						border:1vw solid rgba(102, 214, 246, 0.4);
					}
					&.three{
						border:1vw solid rgba(102, 214, 246, 0.6);
					}
					div.innercircle{
						position: relative;
						width: 100%;
						height: 100%;
						border-radius: 50%;
						border: 1.2vw solid;
						&.outer{
							border-color: white;
							background-color: white;
							&:after{
								position: absolute;
								width: 100%;
								height: 100%;
								border-radius: 50%;
								background-color: rgba(0,0,0,0.15);
								content: '';
								top: 4%;
								left: -3%;
								z-index: -1;
							}
						}
						&.inner{
							border-color: #164a8d;
							box-shadow: inset 0px 0px 29px -5px rgba(0,0,0,0.45);
							background-color: #b2eafb;

							div.register_container{
								position: relative;
								display: flex;
								flex-direction: column;
								justify-content: space-around;
								align-items: center;
								direction: rtl;
								height: 50vh; // for Ios 

								div.input1{
									margin-top: 9vw;
								}

								._register{
									margin-top: 1.8vw;
									width: 82%;
									height: 3.5vw;
									left: 4.8%;
									font-family: jf;
									font-size: 1.7vw;

									display: flex;
									justify-content: initial;
									align-items: center;

									p.textFeild{
										margin: 0;
										width: 12vw;
										text-align: left;
										color: #09316c;
										font-weight: 900;
									}
									input{
										width: 25vw;
									}

									select{
										width: 21vw;
										height: 3.3vw; // for My Mobile aspect 16-9
									}

								}
								select, input{
									border-radius: 1vw;
									color: #5877B7;
									background-color: #FFFFFF;
									border: 0.3vw solid #195091;
									text-align: center;


								}
								.top_sup{
									width: 5%;
									font-size: 2vw;
								}

								div.circle_container{
									position: absolute;
									top: 39vw;
									right: 4vw;
									width: 82%;
									height: 12vh;
									left: 4.8%;
									display: flex;
									justify-content: space-between;
									align-items: center;

									div.outer{
										width: 100%;
										height: 100%;
										border-radius: 50%;
										padding: 0.4vw;
										border: 0.2vw solid rgba(255, 255, 255, 0.62);
									}

									div.circle_Div{
										width: 13vh;
										height: 13vh;
										position: relative;
										display: flex;
										align-items: center;
										justify-content: center;

										p{
											margin: 0;
											font-family: jf;
											font-weight: bold;
											font-size: 3vh;
											color: white;
										}

										div.circle{
											display: flex;
											align-items: center;
											justify-content: center;
										}

										&:nth-child(1){
											div.circle{
												background-color: #e71844;
											}
										}
										&:nth-child(2){
											div.circle{
												background-color: #f7941d;
											}
										}

									}
								}
							}
						}
					}
				}
			}
		}
	}
}
