div.help{
    z-index: 1;
    width: 100%;
    height: 100vh;
    margin: 0px;
    position: relative;

    div.background{
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #f26d7e;
        z-index: 1;

        div.header{
            display: flex;
            justify-content: center;
            align-items: center;

            div.left{
                width: 50%;
                //				position: relative;
                img.logo{
                    margin: 0;
                    width: 20vw;
                    position: absolute;
                    top: 0;
                    left: 7vw;
                    margin-top: 2vw;
                    margin-left: 2vw;
                }
            }

            div.right{
                width: 50%;
                display: flex;
                align-items: center;
                justify-content: flex-end;
                img{
                    float: right;
                    margin: 3vw;
                    width: 20vw;
                }
                div.bigyellowbutton{
                    margin-top: 2vw;
                    margin-right: 2vw;
                    width: 25vw;
                    height: 7vw;
                    background-color: #FEBD10;
                    border-radius: 2vw;
                    padding: .5vw;
                    position: relative;
                    div.smallwhitecircle{
                        background-color: white;
                        width: 1.4vw;
                        height: 1vw;
                        border-radius: 50%;
                        top: 6vw;
                        left: 1.8vw;
                        position: absolute;
                        &:nth-child(2){
                            width: 0.6vw;
                            height: 0.6vw;
                            top: 5.7vw;
                            left: 1.1vw;
                        }
                        &:nth-child(3){
                            width: 0.6vw;
                            height: 0.6vw;
                            top: 1.4vw;
                            left: 23.1vw;
                        }
                        &:nth-child(4){
                            width: 1.7vw;
                            height: 0.9vw;
                            top: 1.1vw;
                            left: 21.2vw;
                        }
                    }
                    div.yellowbutton{
                        width: 100%;
                        height: 100%;
                        border-radius: 2vw;
                        display: flex;
                        border: .3vw dashed white;
                        justify-content: center;
                        align-items: center;
                        p{
                            font-family: jf;
                            text-align: center;
                            color: rgba(0,0,0,0.2);
                            font-size: 4vw;
                            margin: 0;
                            position: relative;
                            &:after{
                                position: absolute;
                                color: white;
                                top: 0;
                                left: 0.5vw;
                                width: 100%;
                                height: 100%;
                                content: 'المساعدة';
                            }
                        }
                    }
                    &:before{
                        position: absolute;
                        top: 0.7vw;
                        left: -0.5vw;
                        width: 24vw;
                        height: 8vw;
                        background-color: rgba(0, 0, 0, 0.2);
                        content: '';
                        z-index: -1;
                        border-radius: 1.2vw;
                    }
                }
            }

        }

        img.star{
            width: 15%;
            position: absolute;
            z-index: 1;

            &:nth-child(2){
                top: 40%;
                left: -8%;
            }
            &:nth-child(3){
                top: 79%;
                left: 5%;
                width: 3%;
            }
            &:nth-child(4){
                top: 20%;
                left: 23%;
                width: 13%;
            }
            &:nth-child(5){
                top: 62%;
                left: 21%;
                width: 12%;
            }
            &:nth-child(6){
                top: 11%;
                left: 45%;
                width: 11%;
            }
            &:nth-child(7){
                top: 58%;
                left: 41%;
                width: 3%;
            }
            &:nth-child(8){
                top: 88%;
                left: 44%;
                width: 7%;
            }
            &:nth-child(9){
                top: 82%;
                left: 50%;
                width: 7%;
            }
            &:nth-child(10){
                top: 39%;
                left: 52%;
                width: 7%;
            }
            &:nth-child(11){
                top: 48%;
                left: 62%;
                width: 5%;
            }
            &:nth-child(12){
                top: 8%;
                left: 65%;
                width: 5%;
            }
            &:nth-child(13){
                top: 30%;
                left: 78%;
                width: 5%;
            }
            &:nth-child(14){
                top: 23%;
                right: 3%;
                width: 6%;
            }
            &:nth-child(15){
                top: 42%;
                right: -4%;
                width: 8%;
            }
            &:nth-child(16){
                bottom: 17%;
                right: 8%;
                width: 21%;
            }
            &:nth-child(17){
                bottom: 8%;
                right: 2%;
                width: 8%;
            }
        }

        div.pen_footer{
            position: absolute;
            bottom: 0;
            width: 100%;
            z-index: 2;
            left: -1vw;
            img{
                width: 100%;
            }

            div.text_footer{
                position: absolute;
                bottom: 0.5vw;
                width: 55%;
                width: 68%;
                p{
                    margin: 0;
                    text-align: center;
                    font-size: 0.9vw;
                    font-weight: bold;
                    font-family: jf;
                    height: 1.1vw;
                }
            }

        }
    }

    div.elementcontainer{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        img.back-select{
            top: 1vw;
            left: 1vw;
            position: absolute;
        }

        div.helprow{
            display: flex;
            justify-content: space-around;
            align-items: center;
            width: 100%;
            margin-bottom: 3vh;
            div.element {
                width: 17vw;
                img{
                    width: 100%;
                }
            }
        }
    }

    div#myModal{
        div.modal-dialog{
            display: flex;
            width: 67%;
            height: 100vh;
            justify-content: center;
            margin: auto;
            align-items: center;
            div.modalimgcontainer{
                position: relative;

                div {
                    &.popuphelp {
                        transform-origin: right;
                        perspective: 600px;
                        div.container {
                            direction: rtl;
                            transform-style: preserve-3d;
                            width: 64vw;
                            height: 34vw;
                            padding: 1vw;
                            background-color: rgba(3, 7, 7, 0.3);
                            transform: rotateX(10deg) rotateY(-7deg);
                            border-radius: 5vw;
                            div.dashborder {
                                border: 0.2vw dashed white;
                                width: 100%;
                                height: 100%;
                                border-radius: 5vw;
                                position: relative;
                                div.innerborder {
                                    color: white;
                                    width: 100%;
                                    height: 100%;
                                    border-radius: 5vw;
                                    padding: 1vw;
                                    position: absolute;
                                    div.smallborder {
                                        width: 100%;
                                        height: 100%;
                                        background-color: #ffc20e;
                                        border-radius: 5vw;
                                        padding: 2vw;
                                        position: relative;
                                        div {
                                            &.symbolecontainer {
                                                position: absolute;
                                                top: 2%;
                                                left: 2%;
                                                &:nth-child(2) {
                                                    top: auto;
                                                    left: auto;
                                                    transform: rotateZ(180deg) scale(0.8);
                                                    right: 2%;
                                                    bottom: -2%;
                                                }
                                                div {
                                                    &.smallcircle {
                                                        border-radius: 50%;
                                                        width: 0.8vw;
                                                        height: 0.8vw;
                                                        background-color: white;
                                                        position: absolute;
                                                        z-index: 5;
                                                        &:nth-child(1) {
                                                            left: 93%;
                                                            top: 5%;
                                                            width: 0.5vw;
                                                            height: 0.5vw;
                                                        }
                                                        &:nth-child(2) {
                                                            left: 72%;
                                                            top: 6%;
                                                            width: 0.8vw;
                                                            height: 0.8vw;
                                                        }
                                                        &:nth-child(3) {
                                                            top: 12%;
                                                            left: 52%;
                                                            width: 0.8vw;
                                                            height: 1vw;
                                                        }
                                                        &:nth-child(4) {
                                                            top: 46%;
                                                            left: 9%;
                                                            height: 0.8vw;
                                                            width: 9.5px;
                                                        }
                                                        &:nth-child(5) {
                                                            top: 5%;
                                                            left: 111%;
                                                            width: .4vw;
                                                            height: .4vw;
                                                        }
                                                    }
                                                    &.symbole {
                                                        z-index: 4;
                                                        position: relative;
                                                        width: 5vw;
                                                        height: 8vw;
                                                        overflow: hidden;
                                                        top: -4vw;
                                                        right: 2vw;
                                                        div.circle {
                                                            width: 7vw;
                                                            height: 6vw;
                                                            border: 1vw solid white;
                                                            border-radius: 50%;
                                                            position: absolute;
                                                            top: 60%;
                                                            left: 49%;
                                                        }
                                                    }
                                                }
                                            }
                                            &.iconcontainer {
                                                padding-top: 3vw;
                                                height: 100%;
                                                font-size: 2vw;
                                                color: #885422;
                                                display: flex;
                                                flex-direction: column;
                                                justify-content: space-around;
                                                //					transform: rotateX(-10deg) rotateY(7deg);
                                                > div {
                                                    display: flex;
                                                    align-items: center;
                                                    .image-container{
                                                        width: 7vw;
                                                        height: 7vw;
                                                        .circle{
                                                            width: 100%;
                                                            height: 100%;
                                                            border-radius: 50%;
                                                            border: 0.4vw solid white;
                                                            overflow: hidden;
                                                            background-color: yellow;
                                                            p{
                                                                font-size: small;
                                                                background: yellow;
                                                            }
                                                        }
                                                    }
                                                    img {
                                                        width: 15vw;
                                                    }
                                                    p {
                                                        padding: 0.5vw 3vw 0.5vw 3vw;
                                                        margin: 0 1vw;
                                                        border-radius: 0.8vw;
                                                        background: linear-gradient(to right, #ffc20e 0%, #fecf5c 100%);
                                                        padding: 1vw;
                                                    }
                                                    &:nth-child(2) p, &:nth-child(4) p, &:nth-child(6) p {
                                                        background: none;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        &.info {
                            padding: 2vw;
                            div.container div.dashborder div.innerborder div.smallborder {
                                background-color: #afca09;
                                div.iconcontainer {
                                    img {
                                        width: auto;
                                        height: 6vw;
                                    }
                                    p {
                                        background: linear-gradient(to left, #c7da0c 0%, #afca09 100%);
                                        color: #35520f;
                                    }
                                    &:nth-child(2) p {
                                        background: none;
                                    }
                                }
                            }
                        }
                        &.storepopup {
                            padding: 2vw;
                            div.container div.dashborder div.innerborder div.smallborder {
                                background-color: #6ac4e0;
                                padding-left: 3vw;
                                p {
                                    border-radius: 0.8vw;
                                    background: 0, 0, 0, 0;
                                    color: #1a6899;
                                    font-size: 2vw;
                                    margin-right: 8vw;
                                    padding: 0;
                                    margin-bottom: -2.4vw;
                                    margin-top: 1vw;
                                }
                                div.iconcontainer {
                                    img {
                                        width: auto;
                                        height: 6vw;
                                    }
                                    p {
                                        background: linear-gradient(to left, #96d4eb 0%, #6ac4e0 100%);
                                        color: #1a6899;
                                    }
                                }
                            }
                        }
                        &.unit {
                            padding: 2vw;
                        }
                    }
                    &.image-container div.circle div.popuphelp.unit div.container div.dashborder div.innerborder div.smallborder {
                        background-color: #003c71;
                    }
                    &.popuphelp {
                        &.unit div.container div.dashborder div.innerborder div.smallborder div.iconcontainer {
                            img {
                                width: auto;
                                height: 6vw;
                            }
                            p {
                                background: transparent;
                                color: white;
                            }
                        }
                        &.icons {
                            padding: 2vw;
                            div.container div.dashborder div.innerborder div.smallborder {
                                padding: 1vw;
                                padding-right: 4vw;
                                background-color: #b8a8db;
                                div.iconcontainer {
                                    font-size: 1.6vw;
                                    img {
                                        width: 6vw;
                                        height: auto;
                                        padding-bottom: 2vw;
                                    }
                                    p {
                                        background: linear-gradient(to left, #aaa0d1 0%, #b8a8db 100%);
                                        color: #4a1768;
                                    }
                                }
                            }
                        }
                        &.titlebar {
                            padding: 2vw;
                            div.container div.dashborder div.innerborder div.smallborder {
                                padding: 1vw;
                                padding-right: 4vw;
                                background-color: #70c8dd;
                                padding-left: 2vw;
                                div.iconcontainer {
                                    font-size: 1.7vw;
                                    img {
                                        width: 4vw;
                                        height: auto;
                                        padding-bottom: 1vw;
                                    }
                                    p {
                                        background: linear-gradient(to left, #8ad7e6 0%, #70c8dd 100%);
                                        color: #0a2e61;
                                    }
                                }
                            }
                        }
                    }
                }

                img {
                    &.modal-dialog-img {
                        width: 100%;
                    }
                    &.close {
                        top: -4%;
                        left: 2.5%;
                        position: absolute;
                        width: 9vw;
                        height: 9vw;
                        background-repeat: no-repeat;
                        opacity: 1;
                    }
                }
            }
        }
    }
}
