@media screen and (orientation: portrait) {
	body{
		display: none;
	}
}



@-ms-viewport {
	width: device-width; ;
	height: device-height; 	
}

html{
	overflow: hidden;
	-ms-content-zooming: none;
	-ms-touch-action: pan-x;
	touch-action: pan-x;
	body{
		//	-ms-scroll-chaining: none;
		//	overflow: hidden;
		//	-ms-content-zooming: none;
		-ms-touch-action: pan-y;
		touch-action: pan-y;
		-ms-content-zooming: none;
		>div{
			//		touch-action: none; 
			overflow: hidden;
			div#ChangeContentDiv{
				height: 100vh;
				position: relative;
				z-index: 4;
				transform: translateX(0);
				transition: 1s transform;
				&.active-menu{
					transform: translateX(-30.5vw);
				}
				div.seperation_layer{
					z-index: 0;
					width: 100%;
					height: 100vh;
					background-color: rgba(0, 0, 0, 0.38);
				}
				>div{
					position: absolute;
					top: 0;
				}
			}
		}
	}
}

