div.master_content{
	width: 100vw;
	height: 75vh;
	z-index: 3;
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;


	div.content_square{
		position: relative;
		overflow: hidden;
		width: 78vw;
		height: 75vh;
		background-color: white;
		border-radius: 1vw;
		z-index: 3;

		div.shadow{
			position: absolute;
			z-index: 1;
			background-color: rgba(0,0,0,0.1);
			width: 100%;
			height: 5%;
			border-radius: 50%;
			top: -3%;
			left: 0;
			right: 0;
			margin: auto;
			filter: blur(3px) grayscale(20%);
		}
	}
}

