// Global Model popup Used at Help Icon for many pages ( there was a page for Help )
div#myModal{
	div.modal-dialog{
		display: flex;
		//        width: 67%;
		height: 100vh;
		justify-content: center;
		margin: auto;
		align-items: center;
		div.modalimgcontainer{
			position: relative;

			div {
				&.popuphelp {
					transform-origin: right;
					perspective: 600px;
					div.container {
						direction: rtl;
						transform-style: preserve-3d;
						width: 64vw;
						height: 44vw;
						padding: 1vw;
						background-color: rgba(3, 7, 7, 0.3);
						transform: rotateX(10deg) rotateY(-7deg);
						border-radius: 5vw;
						div.dashborder {
							border: 0.2vw dashed white;
							width: 100%;
							height: 100%;
							border-radius: 5vw;
							position: relative;
							div.innerborder {
								color: white;
								width: 100%;
								height: 100%;
								border-radius: 5vw;
								padding: 1vw;
								position: absolute;
								div.smallborder {
									width: 100%;
									height: 100%;
									background-color: #ffc20e;
									//                                    background: linear-gradient(to right, #003c71 0%, #002e5b 100%);
									border-radius: 5vw;
									padding: 1.5vw;
									position: relative;
									div {
										&.symbolecontainer {
											position: absolute;
											top: 2%;
											left: 2%;
											&:nth-child(2) {
												top: auto;
												left: auto;
												transform: rotateZ(180deg) scale(0.8);
												right: 2%;
												bottom: -2%;
											}
											div {
												&.smallcircle {
													border-radius: 50%;
													width: 0.8vw;
													height: 0.8vw;
													background-color: white;
													position: absolute;
													z-index: 5;
													&:nth-child(1) {
														left: 93%;
														top: 5%;
														width: 0.5vw;
														height: 0.5vw;
													}
													&:nth-child(2) {
														left: 72%;
														top: 6%;
														width: 0.8vw;
														height: 0.8vw;
													}
													&:nth-child(3) {
														top: 12%;
														left: 52%;
														width: 0.8vw;
														height: 1vw;
													}
													&:nth-child(4) {
														top: 46%;
														left: 9%;
														height: 0.8vw;
														width: 9.5px;
													}
													&:nth-child(5) {
														top: 5%;
														left: 111%;
														width: .4vw;
														height: .4vw;
													}
												}
												&.symbole {
													z-index: 4;
													position: relative;
													width: 5vw;
													height: 8vw;
													overflow: hidden;
													top: -4vw;
													right: 2vw;
													div.circle {
														width: 7vw;
														height: 6vw;
														border: 1vw solid white;
														border-radius: 50%;
														position: absolute;
														top: 60%;
														left: 49%;
													}
												}
											}
										}
										&.iconcontainer {
											padding-top: 3vw;
											height: 100%;
											font-size: 2vw;
											color: #885422;
											display: flex;
											flex-direction: column;
											justify-content: space-around;
											//					transform: rotateX(-10deg) rotateY(7deg);
											> div {
												display: flex;
												align-items: center;
												.image-container{
													width: 6vw;
													height: 6vw;
													.circle{
														width: 100%;
														height: 100%;
														border-radius: 50%;
														border: 0.2vw solid white;
														overflow: hidden;
														background: #fec20d;
														display: flex;
														align-items: center;
														p{
															//															font-size: x-small;
															font-size: 1vw;
															background: #fec20d;
															padding-right: 0vw;
															color: white;
															font-family: jf;                                                                                                      
														}
													}
												}
												img {
													width: 15vw;
												}
												p {
													padding: 0.5vw 3vw 0.5vw 3vw;
													margin: 0 1vw;
													border-radius: 0.8vw;
													background: linear-gradient(to right, #ffc20e 0%, #fecf5c 100%);
													padding: 1vw;
												}
												&:nth-child(2) p, &:nth-child(4) p, &:nth-child(6) p {
													background: none;
													width: 85%
												}
											}
										}
									}
								}
							}
						}
					}
					&.info {
						padding: 2vw;
						div.container div.dashborder div.innerborder div.smallborder {
							background-color: #afca09;
							div.iconcontainer {
								img {
									width: auto;
									height: 6vw;
								}
								p {
									background: linear-gradient(to left, #c7da0c 0%, #afca09 100%);
									color: #35520f;
								}
								&:nth-child(2) p {
									background: none;
								}
							}
						}
					}
					&.storepopup {
						padding: 2vw;
						div.container div.dashborder div.innerborder div.smallborder {
							background-color: #6ac4e0;
							padding-left: 3vw;
							p {
								border-radius: 0.8vw;
								background: 0, 0, 0, 0;
								color: #1a6899;
								font-size: 1.8vw;
								margin-right: 8vw;
								padding: 0;
								margin-bottom: -2.4vw;
								margin-top: 1vw;
							}
							div.iconcontainer {
								img {
									width: 6vw;
									/* height: 6vw; */
									margin-right: 1vw;
								}
								p {
									background: linear-gradient(to left, #96d4eb 0%, #6ac4e0 100%);
									color: #1a6899;
									// added for last paragraph at store help icon
									width: 85%;
								}
							}
						}
					}
					&.unit {
						padding: 2vw;                       
					}
				}
				&.image-container div.circle div.popuphelp.unit div.container div.dashborder div.innerborder div.smallborder {
					background-color: #003c71;
				}
				&.popuphelp {
					&.unit div.container div.dashborder div.innerborder div.smallborder{
						background-color: #002e5b;
						background: linear-gradient(to left, #0f5599 0%, #001e3a 100%);
						div.iconcontainer {
							img {
								width: 22vw;
								margin-right: 1vw;
							}
							p {
								background: transparent;
								color: white;
							}
						}
					}
					&.icons {
						padding: 2vw;
						div.container div.dashborder div.innerborder div.smallborder {
							padding: 1vw;
							padding-right: 4vw;
							background-color: #b8a8db;
							div.iconcontainer {
								font-size: 1.6vw;
								img {
									width: 6vw;
									height: auto;
									padding-bottom: 2vw;
								}
								p {
									background: linear-gradient(to left, #aaa0d1 0%, #b8a8db 100%);
									color: #4a1768;
								}
							}
						}
					}
					&.titlebar {
						padding: 2vw;
						div.container div.dashborder div.innerborder div.smallborder {
							padding: 1vw;
							padding-right: 4vw;
							background-color: #70c8dd;
							padding-left: 2vw;
							div.iconcontainer {
								font-size: 1.7vw;
								img {
									width: 4vw;
									height: auto;
									padding-bottom: 1vw;
								}
								p {
									background: linear-gradient(to left, #8ad7e6 0%, #70c8dd 100%);
									color: #0a2e61;
									width: 85%;
								}
							}
						}
					}
				}
			}

			img {
				&.modal-dialog-img {
					width: 100%;
				}
				&.close {
					top: -4%;
					left: 2.5%;
					position: absolute;
					width: 9vw;
					height: 9vw;
					background-repeat: no-repeat;
					opacity: 1;
				}
			}
		}
	}
}

