div.Panel_goto{
	position: absolute;
	transition-property: width, right, left;
	transition: 1s;
	z-index : 4;
	overflow: hidden;
	width: 23.3vw;
	height: 9vw;
	top: 50vh;
	right: -23.3vw;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;


	div.goToBtns{
		width: 4.5vh;
		height: 4.5vh;
	}

	div.top{
		width: 100%;
		height: 50%;
		background-color: #0072bc;
		border-radius: 1vw;
		z-index: 3;
		border: 0vw solid #0072bc;
		box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.51);

		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		div.goToBtns{
			&.btn_goto_pg_study{
				left: 1vw;
				top: 1vw;
				position: absolute;
				background-size: cover;
				background-repeat: no-repeat;
				background-image: url("../images/btn_goto_pg.png");
			}
		}

		input.text_area_study{
			//						position: absolute;
			font-size: 2vw;
			text-align: center;
			direction: rtl;
			&.eng{
				transform: scaleX(-1);
				direction: ltr;
			}
		}
	}

	div.bottom{
		width: 84%;
		height: 45%;
		background-color: #0072bc;
		border-radius: 0.3vw 0.3vw 1vw 1vw;
		z-index: 2;
		border: 0vw solid #0072bc;

		display: flex;
		align-items: center;
		justify-content: space-around;
		flex-direction: row;

		div.goToBtns{
			//						position: absolute;
			background-size: 100% 100%;
			background-repeat: no-repeat;

			&.btn_prev_pg_study{
				background-image: url("../images/btn_goto_prev.png");
			}
			&.btn_next_pg_study{
				background-image: url("../images/btn_goto_next.png");
			}
			&.btn_first_pg_study{
				background-image: url("../images/btn_goto_first.png");
			}
			&.btn_last_pg_study{
				background-image: url("../images/btn_goto_last.png");
			}
		}
	}

	&.active{
		width: 20.3vw;
		right: 0vw;
	}
	&.eng{
		transform: scaleX(-1);
