.sticky {
	/*	z-index: 2;*/
	-webkit-touch-callout: initial; /* iOS Safari */
	-webkit-user-select: initial;   /* Chrome/Safari/Opera */
	-khtml-user-select: initial;    /* Konqueror */
	-moz-user-select: initial;      /* Firefox */
	-ms-user-select: initial;       /* Internet Explorer/Edge */
	user-select: initial;
	z-index: 10;
	width:29.3vw;
	background:#fdfdbe;
	box-shadow:3px 3px 10px rgba(0,0,0,0.45);
	-webkit-box-shadow:3px 3px 10px rgba(0,0,0,0.45);
	-moz-box-shadow:3px 3px 10px rgba(0,0,0,0.45);

	.sticky-header {
		padding:0.8vw;
		background:#f3f3f3;
		border-bottom:2px solid #fefefe;
		box-shadow:0 3px 5px rgba(0,0,0,0.25);
		-webkit-box-shadow:0 3px 5px rgba(0,0,0,0.25);
		-moz-box-shadow:0 3px 5px rgba(0,0,0,0.25);

		.sticky-status {
			color:#ccc;
			padding:5px;
		}
		.close-sticky {
			background:#474747;
			float:right;
			cursor:default;
			color:#ececec;
			padding:1px 5px;
			border-radius:20px;
			-webkit-border-radius:20px;
			-moz-border-radius:20px;
		}

		.save-sticky {
			background:#474747;
			cursor:default;
			color:#ececec;
			padding: 2px 7px;
			border-radius:20px;
			-webkit-border-radius:26px;
			-moz-border-radius:26px;
		}
	}

	.sticky-content {

		-webkit-touch-callout: initial; /* iOS Safari */
		-webkit-user-select: initial;   /* Chrome/Safari/Opera */
		-khtml-user-select: initial;    /* Konqueror */
		-moz-user-select: initial;      /* Firefox */
		-ms-user-select: initial;       /* Internet Explorer/Edge */
		user-select: initial;
		min-height:15vw;
		transition: 1s;		
		border-left:3px double rgba(238, 150, 122, .75);
		margin-left: 3vw;
		padding:5px;
		word-wrap: break-word;
	}
	.maximized {
		min-height:15vw;
	}

	.minimized{
		min-height:'0px';
	}

	.footer-sticky {
		padding:0.8vw;
		background:#f3f3f3;
		border-bottom:2px solid #fefefe;
		box-shadow:0 3px 5px rgba(0,0,0,0.25);
		-webkit-box-shadow:0 3px 5px rgba(0,0,0,0.25);
		-moz-box-shadow:0 3px 5px rgba(0,0,0,0.25);

		.sticky-status {
			color:#ccc;
			padding:5px;
		}

		.minimize-sticky {

			background: #474747;
			float: right;
			cursor: default;
			color: #ececec;
			padding: 1px 5px;
			border-radius: 20px;
			-webkit-border-radius: 20px;
			-moz-border-radius: 20px;
		}

		.maximize-sticky {
			display: none;
			background: #474747;
			float: right;
			cursor: default;
			color: #ececec;
			padding: 1px 5px;
			border-radius: 20px;
			-webkit-border-radius: 20px;
			-moz-border-radius: 20px;
		}
	}

}

